header {
  justify-content: space-between;
  align-items: center;
  width: auto;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  font-size: 12px;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  padding: 0 3px;
}

.card__image img {
  width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  margin-top: 36px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__review-count {
  color: #060b35;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: flex-end;
  height: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  line-height: 18px;
  display: flex;
}

.card__price-name {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transition: all .3s;
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

* {
  box-sizing: border-box;
  margin: 0;
}

html {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

body {
  background-color: #fff;
  border: 0;
  padding: 0;
}

.content {
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .content {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .content {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .content {
    grid-template-columns: repeat(4, 200px);
  }
}

